.App {
  color:#252324
}

.fp-watermark {
  display: none;
  }

.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: -1;
}

.contents {
  /* position: relative; Background 위에 올라오도록 설정 */
  z-index: 1;
}

.profile{
  width: 20vw;
  
  display: flex;
  flex-direction: column;
  align-items: center;

  position: fixed;
  top: 20vh;
  left: 3vw;
  z-index: 10;
}

.section {
  margin-left: 12vw;
}



.background .vector {
  left: 0;
  position: fixed;
  width: 100vw;
}

.background .vector:first-child {
  top: 10vh;
}

.background .vector:last-child {
  top: 65vh;
}

.background .miniflower, .background .miniminiflower {
  position: fixed;
  transition: transform 0.5s ease-in-out; /* Smooth transition for position change */
}

