.profile .name {
  font-size: 8vh;
  font-family: "Crimson Text", serif;
  font-weight: 700;
  font-style: italic;
  margin-bottom: 8%;
}

.profile .flowerimg {
  width: 18vw; /* profile div의 80% 너비 */
  height: auto; /* 이미지의 비율을 유지하면서 너비에 맞춰 높이 조절 */
}

.profile .intro{
  display: flex; /* Flexbox 레이아웃 사용 */
  flex-direction: column;
  align-items: center;
}

.profile .intro p a:hover{
  animation: blink-pink 0.5s ease forwards; /* 애니메이션 적용 */
  color:#E071A7;
}

.profile .intro .name {
  font-size: 2vw;
  font-family: "Crimson Text", serif;
  font-weight: 700;
  font-style: normal;

  margin-bottom: 1vh;
}

.profile .intro .email{
  font-size: 1vw;
  font-family: "Crimson Text", serif;
  font-weight: 400;
  font-style: normal;

  margin-top:1vh;
  margin-bottom:1vh;
}

.profile .intro .link{
  font-size: 1.5vw;
  font-family: "Crimson Text", serif;
  font-weight: 400;
  font-style: normal;

  margin-top:0.6vh;
  margin-bottom:1vh;
}

.profile .intro .link-cv{
  font-size: 1.5vw;
  font-family: "Crimson Text", serif;
  font-weight: 600;
  font-style: normal;

  margin-top:1vh;
  margin-bottom:0vh;
}

.profile .intro .lastupdated{
  font-size: 0.8vw;
  font-family: "Crimson Text", serif;
  font-weight: 400;
  font-style: normal;
  color:#a2a2a2;

  margin-top:2.5vh;
  margin-bottom:1vh;
}

.profile .intro p a{
  color: inherit;
  text-decoration: none;
}


.research-container{
  display: flex;
  align-items: center;
  gap:3vw;
  margin-bottom: 5vh;
  padding-left: 0.5vw;
}

.research-container img{
  width:26vh;
  border: 1px solid #FFC9E3;
}

/* .research-container .desc{
  padding-left:14vw;
} */

.research-container .desc p{
  margin-top:0vh;
  margin-bottom: 0.5vh;
}

.research-container .desc p:first-child{
  font-size: 1vw;
  font-family: "Crimson Text", serif;
}

.research-container .desc p:nth-child(2){
  font-size:1.2vw;
  font-weight: 700;
}

.research-container .desc p:nth-child(3){
  font-size:1vw;
}

.menu {
  align-items: flex-start;
  display: inline-flex;
  gap: 1vw;
  position: relative;
  margin-top: 0.5vh;
  color:#E071A7;
}

.menu span{
  font-size: 0.8vw;
  font-family: "Crimson Pro", serif;
  font-weight: 500;
  font-style: normal;
}

.menu a{
  text-decoration: none;
  color: inherit;
}

.menu a:hover{
  color: #CCB2BF;
  animation: blink-lightpink 0.5s ease forwards;
}


.contents-desc{
  padding-left: 14vw;
  padding-right: 23vw;
}

.contents-desc p{
  font-size: 2.4vh;
  font-family: "Crimson Pro", serif;
  font-weight: 400;
  font-style: normal;
}

.contents-desc .name-tag{
  margin-bottom: 5vh;
  
}

.contents-desc .introdesc p{
  margin-top: 1vh;
  margin-bottom: 1vh;

  /* additional padding */
  padding-right: 13vw;
}


.contents-desc .name-tag p:first-child{
  font-size: 4vw;
  font-family: "Crimson Text", serif;
  font-weight: 700;
  font-style: italic;
  margin: 0vh;
}

.contents-desc .name-tag p:last-child{
  font-size: 1.5vw;
  font-family: "Crimson Text", serif;
  font-weight: 500;
  font-style: italic;
  margin-top: 0vh;
}

.contents-desc .subresearch{
  margin-bottom: 10vh;
}

.contents-desc .research-title{
  font-size: 3vw;
  font-family: "Crimson Text", serif;
  font-weight: 700;
  font-style: italic;

  margin: 0vh;
  margin-top: 15vh;
}

.contents-desc .subtitle{
  font-size: 1.3vw;
  font-family: "Crimson Text", serif;
  font-weight: 400;
  font-style: italic;
  /* margin: 0vh; */
}

.contents-desc button{
  border: none;
  outline: none;
  background-color: inherit ;
  cursor: pointer;

  /* font-size: 2.2vh; */
}

.contents-desc .introdesc .publication {
  font-size: 1.4vw;
  font-family: "Crimson Pro", serif;
  font-weight: 700;
  font-style: normal;

  padding: 0;

  transition: all 0.3s ease; /* Smooth transition for hover effect */
}

.contents-desc p .exp-bold{
  font-family: "Crimson Text", serif;
  font-weight: 700;
  font-style: normal;
}

.contents-desc p .exp-semibold{
  font-family: "Crimson Text", serif;
  font-weight: 600;
  font-style: normal;
  
}

.contents-desc p .exp-italic{
  font-family: "Crimson Text", serif;
  font-weight: 400;
  font-style: italic;
}

.contents-desc p .exp-boltalic{
  font-family: "Crimson Text", serif;
  font-weight: 700;
  font-style: italic;
}

.contents-desc p .exp-semiboltalic{
  font-family: "Crimson Text", serif;
  font-weight: 600;
  font-style: italic;
}

.contents-desc p .exp-under{
  font-family: "Crimson Text", serif;
  font-weight: 400;
  font-style: normal;
  text-decoration: underline;
}

.contents-desc p a {
  text-decoration: none;
  color: inherit;
}

.contents-desc p a:hover{
  color:#E071A7;
  /* transition: color 0.3s ease; 기본 색 전환 효과 */
  animation: blink-pink 0.5s ease forwards; /* 애니메이션 적용 */
}

.contents-desc p button:hover{
  color:#E071A7;
  /* transition: color 0.3s ease; 기본 색 전환 효과 */
  animation: blink-pink 0.5s ease forwards; /* 애니메이션 적용 */
}

