/* 깜빡임 애니메이션 정의 */
@keyframes blink-pink {
  0% {
    color: black; /* 시작 시 검정색 */
  }
  50% {
    color: #e071a7ce; /* 중간에 분홍색 */
  }
  100% {
    color: #E071A7; /* 최종적으로 분홍색 */
  }
}

@keyframes blink-lightpink {
  0% {
    color: #E071A7; /* 시작 시 검정색 */
  }
  50% {
    color: #CCB2BF;; /* 중간에 분홍색 */
  }
  100% {
    color: #CCB2BF; /* 최종적으로 분홍색 */
  }
}